<template>
	<div id="shoppingCart">
		<div class="header">
			<div class="icon-left" @click="goBack()"><img src="../../static/img/icon/release_icon/return@2x.png"/></div>
			<div>购物车</div>
			<div style="width: 40px;"></div>
		</div>
		<div class="header-null"></div>
		<div class="shopping-list" v-if="list.length != 0" v-for="(i,index) in list">
			<div class="title">
				<img src="../../static/img/icon/my_icon/merchant@3x.png"/>
				{{i.createName}}
				<img src="../../static/img/icon/my_icon/arrow@3x.png"/>
			</div>
			<div class="shopping-list-item mui-table-view-cell" v-for="(item,index) in i.shoppingList" v-if="!item.dele">
				<div class="mui-slider-handle">
					<div class="shopping-list-item-content">
						<div class="shopping-list-item-check">
							<img src="../../static/img/icon/release_icon/weixuanzhong@2x.png" v-if="!item.check" @click="checkItem(item,index)"/>
							<img v-else src="../../static/img/icon/release_icon/nextstep@2x.png"  @click="checkItem(item,index)"/>
						</div>
						<div>
							<div style="flex: 1;">
								<img :src="item.titleUrl.split(',')[0]"/>
							</div>
							<div>
								<div class="shopping-list-item-content-title">
									{{item.commodityName}}
								</div>
								<div class="shopping-list-content-type">
									{{item.sortName}}
								</div>
								<div class="shopping-list-content-bottom">
									<div class="shopping-list-content-num-left">
										￥{{item.sortPrice}}
									</div>
									<div class="shopping-list-content-num-right">
										<div class="shopping-list-content-num-reduce" @click.stop="reduce(item)">
											-
										</div>
										<div class="shopping-list-content-number">
											{{item.buyCount}}
										</div>
										<div class="shopping-list-content-num-plus" @click.stop="plus(item)">
											+
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="mui-slider-right mui-disabled" ref="slider" @click="deleted(index)">
					<a class="mui-btn mui-btn-red">删除</a>
				</div>
				<!--<div class="mui-slider-handle">
					左滑显示删除按钮
				</div>-->
			</div>
		</div>
		<div class="shopping-null" v-if="list.length == 0">
			<div>
				<img src="../../static/img/icon/find_icon/gouweche.png"/>
			</div>
			<div>
				啊噢~您的购物车还没有商品
			</div>
			<div>
				可以逛逛有哪些想买的
			</div>
			<div @click="goIndex">
				去购物
			</div>
		</div>
		<div class="shopping-fixed" v-if="list.length != 0">
			<div class="shopping-fixed-check">
				<img src="../../static/img/icon/release_icon/weixuanzhong@2x.png" v-if="!check" @click="checkAll"/>
				<img v-else src="../../static/img/icon/release_icon/nextstep@2x.png"  @click="checkAll"/>
				全选
			</div>
			<div>
				<div>合计：<span>￥{{total}}</span></div>
				<div>不含运费</div>
			</div>
			<div class="shopping-fixed-btn" @click="submit">
				结算({{checkTrue.length}})
			</div>
		</div>
	</div>
</template>

<script>
	import {getData} from '../service/getData'
	export default {
		data() {
			return {
				num: 1,
				check: false,
				list: [],
				updataList: [],
				checkTrue: [],
			}
		},
		methods: {
			reduce(item) { //数值减
				if(item.buyCount < 2) {
					item.buyCount = 1
				} else {
					item.buyCount--;					
				}
			},
			goIndex() {
				getData('/shoppingtrolley/refreshShoppingTrolleyApi', {
					userId: sessionStorage.userId,
					shoppingList: JSON.stringify(this.updataList)
				}).then(d => {
					
				})
				sessionStorage.index = 2
				this.route('index')
			},
			goBack() {
				getData('/shoppingtrolley/refreshShoppingTrolleyApi', {
					userId: sessionStorage.userId,
					shoppingList: JSON.stringify(this.updataList)
				}).then(d => {
					
				})
				if(sessionStorage.state == 1) {
					window.android.colseActivity()
				} else {
					this.$router.goBack()					
				}
			},
			plus(item) {	//数值加				
				item.buyCount++
			},
			checkItem(item, index) { //每个列表的状态
				item.check = !item.check
				if(item.check) {
					this.checkTrue.push(item)
				} else {
					for(var i=0;i<this.checkTrue.length;i++) {
						if(item.ShoppingTrolleyID ==this.checkTrue[i].ShoppingTrolleyID) {
							this.checkTrue.splice(i,1)
						}
					}
				}
			},
			deleted(index) {
				var _this = this
				mui.confirm('确定删除该商品','',['确定','取消'],function(e) {
					if(e.index == 0) {
						_this.list[index].dele = true
						setTimeout(function() {
							_this.list.splice(index, 1)
						})
					}
				},'div')
			},
			checkAll() {  //选择全部
				this.check = !this.check
				if(this.check) {
					this.checkTrue = []
					this.list.forEach(x => {
						x.shoppingList.map(i => {
							i.check = true
							this.checkTrue.push(i)							
						})
					})
				} else {
					this.checkTrue = []
					this.list.forEach(x => {
						x.shoppingList.map(i => {
							i.check = false						
						})
					})
				}
			},
			init() {
				this.$store.state.isLoading = true
				getData('/shoppingtrolley/getShoppingByUserIdApi', {
					userId: sessionStorage.userId,
				}).then(d => {
//					d.groupByList.map(x => {
//						x.dele = false
//					})
					this.$store.state.isLoading = false
					if(d.groupByList) {
						this.list = d.groupByList
						let _this = this
						this.list.map(x => {
							x.shoppingList.map(i => {
								_this.updataList.push(i)
							})
						})								
					}
				}).catch(err => {
					this.$store.state.isLoading = false
					console.log(err)
				})
			},
			submit() {
				this.$store.state.isLoading = true
				getData('/shoppingtrolley/refreshShoppingTrolleyApi', {
					userId: sessionStorage.userId,
					shoppingList: JSON.stringify(this.updataList)
				}).then(d => {
					
				})
				if(this.checkTrue.length == 0) {
					alert('请选择商品')
				} else {
					var id = ''
					this.checkTrue.map(x => {
						id += x.ShoppingTrolleyID + ','
					})
					localStorage.shoppingIds = id
					getData('/shoppingtrolley/settleAccountsApi', {
						userId: sessionStorage.userId,
						shoppingIds: id 
					}).then(d => {
						this.$store.state.isLoading = false
						if(d.code == 1) {
							this.route('order')
						} else {
							alert(d.msg)
						}
					}).catch(err => {
						this.$store.state.isLoading = false
						console.log(err)
					})					
				}
			}
		},
		computed: {
			total() {
				var sum = 0;
	        	for(var i= 0 ;i< this.checkTrue.length; i++) {
	        	    sum += parseInt(this.checkTrue[i].buyCount*this.checkTrue[i].sortPrice);
	        	}
		        return sum;
			}
		},
		watch: {
			checkTrue() {
				if(this.checkTrue.length === this.updataList.length) {
					this.check = true
				} else {
					this.check = false
				}
			}
		},
		mounted() {
			$('#app').scrollTop(0),
			this.init()
		}
	}
</script>

<style scoped lang="less">
	#shoppingCart {
		height: 100%;
	}
	
	/*隐藏滚动条*/
	#shoppingCart::-webkit-scrollbar {display:none}

	.shopping-list {
		background: #fff;
		margin-bottom: 10px;
		
		.title {
			padding: 10px 150px 10px 15px;
			border-bottom: 1px solid #eee;
			display: flex;
			align-items: center;
			
			img {
				width: 20px;
				height: 20px;
				margin-right: 20px;
				
				&:last-child {
					width: 8px;
					height: 12px;
					margin-left: 20px;
				}
			}
		}
		
		.shopping-list-item {
		
			.shopping-list-item-content {
				flex: 5;
				display: flex;
				justify-content: space-between;
				align-items: center;
				
				img {
					width: 90px;
					height: 85px;
					margin-right: 10px;
				}
				
				.shopping-list-item-check {
					flex: 1;
					text-align: center;
					
					img {
						width: 20px;
						height: 20px;
					}
				}
				
				div {
					font-size: 10px;
					position: relative;
					display: flex;
					flex: 10;
					
					div {
						display: flex;
						flex-direction: column;
						
						.shopping-list-item-content-title {
							display: -webkit-box;
							word-break: break-all;
							text-overflow: ellipsis;
							font-size: 32rpx;
							overflow: hidden;
							margin-bottom: 5px;
							-webkit-box-orient: vertical;
							-webkit-line-clamp:2; 
						}
						
						.shopping-list-content-type {
							color: #7B7B7B;
						}
						
						.shopping-list-content-bottom {
							/*position: absolute;
							bottom: 0;*/
							display: flex;
							width: 100%;
							flex-direction: row;
							align-items: center;
							justify-content: space-between;
							
							.shopping-list-content-num-left {
								color: #c9eb59;
								font-size: 13px;
							}
							
							.shopping-list-content-num-right {
								display: flex;
								flex-direction: row;
								text-align: center;
								
								.shopping-list-content-num-reduce {
									background: #eee;
									padding: 2px 6px;
									margin-right: 5px;
								}
								
								.shopping-list-content-number {
									background: #eee;
									padding: 2px 10px;
									margin-right: 5px;
								}
								
								.shopping-list-content-num-plus {
									background: #eee;
									padding: 2px 6px;
								}
							}
						}
						
					}
				}
			}
		}
	}
	
	.shopping-null {
		display: flex;
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		justify-content: center;
		text-align: center;
		flex-direction: column;
		align-items: center;
		font-size: 14px;
		
		div {
			margin-bottom: 5px;
		}
		
		div:nth-child(2) {
			color: #7B7B7B;
		}
		
		img {
			width: 40%;
			height: 40%;
		}
		
		div:last-child {
			padding: 2px 15px;
			background: #bae416;
			border-radius: 5px;
			color: #fff;
		}
	}
	
	.shopping-fixed {
		position: fixed;
		bottom: 0;
		width: 100%;
		border-top: 1px solid #eee;
		display: flex;
		align-items: center;
		font-size: 12px;
		background: #fff;
		
		.shopping-fixed-check {
			flex: 2;
			display: flex;
			align-items: center;
			
			img {
				width: 20px;
				height: 20px;
				margin: 0 10px;
			}
		}
		
		div:nth-child(2) {
			flex: 2;
			text-align: right;
			padding: 0 10px;
			
			span {
				color: red;
				font-weight: bold;
			}
			
			div:last-child {
				font-size: 10px;
				color: #e5e5e5;
			}
		}
		
		.shopping-fixed-btn {
			background: #ff1747;
			padding: 10px 30px;
			color: #fff;
		}
	}

</style>


